body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.custom-header{
	margin-bottom: 0px;
	height: 50px;
}
.display-center{
	text-align: center;
}

.custom-dashboard{
	/* margin-top: 25px; */
	max-width: 900px;
	margin: 25px auto;
	
}
.display-mid p{
	font-size: small;
	margin-bottom: 2px;
}
.custom-panel{
	max-height: 20vh;
	padding: 1px;

}
.custom-panel-body{
	text-align: center;
	padding: 5px;
	max-height: inherit;
}
p{
	font-size: xx-small;
	margin: 0;
}
h5{
	margin: 0;
}
.wrapper {
	/* display: flex; */
	/* align-items: center; */
	margin: auto;
	position: absolute;
	width: 92vw;
	top: 50px;
	left: 50px;
}

#sidebar {
	width: 50px;
	height: 100vh;
	background-color: aqua;
	position: fixed;
	top: 50px;
	z-index: 999;
}
.spacing{
	padding: 4px;
	margin-bottom: 8px;
}
